import { FC, PropsWithChildren, forwardRef } from 'react';
import { EventHandler, TwBaseProps } from '../../types';
import { Collapse as MantineCollapse, CollapseProps as MantineCollapseProps } from '@mantine/core';
import { styled } from '../../utils';

export interface CollapseProps
  extends TwBaseProps,
    PropsWithChildren,
    Pick<
      MantineCollapseProps,
      | 'in'
      | 'transitionDuration'
      | 'transitionTimingFunction'
      | 'animateOpacity'
      | 'onTransitionEnd'
    >,
    Omit<EventHandler<React.ComponentPropsWithoutRef<'div'>>, 'onTransitionEnd'> {
  children: React.ReactNode;
}

const Root = styled(MantineCollapse)<MantineCollapseProps>(() => ({}));

export const Collapse: FC<CollapseProps> = forwardRef((props, ref) => {
  return <Root {...props} ref={ref} />;
});
